.home-svg {
  transform: translate3d(0,0,200px);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  svg {
    overflow: visible;
    rect {
      opacity: 0;
      transform: translate3d(0,0,0) scale(.3);
      transition: opacity 400ms, transform ease 400ms;
    }
  }

  &.start {

    svg {
      rect {
        transform: translate3d(0,0,0) scale(1);
        opacity: 1;
      }
    }
  }

  &.bang-prepare {
    svg {
      rect {
        transform: translate3d(0,0,0) scale(.6);
        transform-origin: center;
        opacity: 1;
      }
    }
  }
}