.slider {
  width: 100%;
  height: 100%;
  position: absolute;
  display: flex;

  .slide {
    min-width: 100%;
    overflow: hidden;
    display: flex;
    justify-content: flex-end;
    pointer-events: none;
    img,
    .canvas-container {
      height: 100%;
      position: absolute;
      top: 0;
      left: calc((100% - 1200px)/2 + 1200px*0.4 + 3px);
    }
  }
}
.canvas-container {
  background-repeat: no-repeat;
  background-size: 100% 100%;
  //image-rendering: pixelated;
}