.photo {
  padding: 100px 0 107px;
  overflow-y: hidden;
  height: 800px;
  margin-bottom: 108px;
  &:before {
    content: '';
    position: absolute;
    width: 100%;
    height: calc(100% - 107px);
    background: $bg-grey;
    top: 0;
    left: 0;
    z-index: -1;
  }
  @include breakpoint($mq-mobile) {
    padding: 0 0 270px;
    margin: 0;
    background: $bg-grey;
    height: auto;
    &:before {
      content: none;
    }
  }

  .slider-controls {
    position: absolute;
    bottom: 0;

    @include breakpoint($mq-mobile) {
      width: 100%;
      bottom: 245px;
      left: 0;
      display: flex;
      justify-content: space-between;
    }
  }


  &-slider.slider {
    width: 60%;
    left: 0;
    top: 0;
    padding-top: 100px;
    @include breakpoint($mq-mobile) {
      height: 270px;
      bottom: 0;
      position: absolute;
      width: 100%;
      top: auto;
      padding-top: 0;
      img {
        left: auto;
        right: auto !important;
        width: auto !important;
        height: 100%;
      }
      .canvas-container {
        left: auto;
        right: auto !important;
        width: 100% !important;
        height: 100%;

      }
    }

    .slide {
      justify-content: flex-start;
      position: relative;

      img {
        left: auto;
        position: absolute;
        //right: calc((100% - 1200px)/2 + 1200px*0.3 + 3px);
        //min-width: calc(100vw * 0.6);
        width: 100%;
      }
      .canvas-container {
        position: absolute;
        top: 0;
        left: 0;
        //width: calc(100vw * 0.6);
        width: 100%;
      }
    }
  }

  &-article {
    width: 30%;
    float: right;
    padding-left: 55px;
    padding-top: 87px;

    .pixel-head {
      margin-bottom: 60px;
      position: relative;
      left: -14px;
      @include breakpoint($mq-mobile) {
        margin-bottom: 30px;
      }

      svg {
        fill: $clr-white;
      }
    }

    .separator {
      width: 30px;
      height: 30px;
      position: relative;
      margin-top: 60px;
      @include breakpoint($mq-mobile) {
        width: 20px;
        height: 20px;
        margin-top: 25px;
      }
      &:after,
      &:before,
      &>div:after {
        content: '';
        display: block;
        width: 33.33%;
        height: 33.33%;
        background: $clr-white;
        position: absolute;
      }
      &:before {
        height: 66.66%;
        left: 0;
        top: 33.33%;
      }
      &:after {
        width: 66.66%;
        left: 0;
        top: 33.33%;
      }
      &>div:after {
        left: 66.66%;
        top: 0;
      }
    }

    p {
      color: $clr-white;
      font-family: $font;
      font-size: 16px;
      margin-bottom: 20px;
    }

    @include breakpoint($mq-mobile) {
      padding: 25px 0;
      width: 100%;
      p {
        font-size: 14px;
      }
    }

  }

  &-cover {
    position: absolute;
    width: 100%;
    height: 100%;


    .cluster {
      position: absolute;
      width: 6px;
      height: 6px;
      background: $clr-white;
      left: auto;
      @for $i from 1 through 11 {
        &_#{$i} {
          opacity: random();
          right: random(350)+ px;
          top: random(600) + px;
        }
      }
    }
  }

}

.photo {

  .crab-svg {
    overflow: visible;
    position: relative;
    z-index: 1;
    transform: translateX(-100%);
    transition: transform steps(11, end) 1100ms 1500ms;
    rect {
      transition: transform ease 900ms 600ms, opacity ease 900ms 600ms;

      @for $i from 1 through 17 {
        &:nth-child(#{$i}) {
          transform: translate3d( random(120)-60+px , random(120)-60+px ,0) ;
          opacity: random();


          &.long {
            transform: translate3d( random(120)-60+px , random(120)-60+px ,0) scaleX(.4);
            opacity: random();
          }
        }
      }

      &.red-eyes {
        fill: $clr-red;
        opacity: 0 !important;
      }
    }

    & + .crab-rocket {
      opacity: 0;
    }
  }


  &[data-fired="1"] {

    .crab-svg {
      transform: translateX(0);
      rect,
      rect.long {
        transform: translate3d(0,0,0) scaleX(1);
        opacity: 1;

        &.move-2-px-l {
          animation-name: move-2-px-l;
          animation-duration: 220ms;
          animation-delay: 1500ms;
          animation-iteration-count: 5;
          animation-timing-function: step-start;
        }
        &.move-2-px-r {
          animation-name: move-2-px-r;
          animation-duration: 220ms;
          animation-delay: 1500ms;
          animation-iteration-count: 5;
          animation-timing-function: step-start;
        }
        &.move-1-px-l {
          animation-name: move-1-px-l;
          animation-duration: 220ms;
          animation-delay: 1500ms;
          animation-iteration-count: 5;
          animation-timing-function: step-start;
        }
        &.move-1-px-r {
          animation-name: move-1-px-r;
          animation-duration: 220ms;
          animation-delay: 1500ms;
          animation-iteration-count: 5;
          animation-timing-function: step-start;
        }
      }

      &.crab-fire-prepare {
        rect.red-eyes {
          opacity: 1 !important;
        }

        & + .crab-rocket {
          opacity: 1;

        }
      }
      &.crab-fire-start {

        & + .crab-rocket {
          opacity: 1;

        }
      }

      &.animation-done {
        rect:nth-child(6) {
          transform-origin: top;
          animation-name: clip;
          animation-iteration-count: 1;
          animation-timing-function: ease;
          animation-duration: 300ms;
          animation-delay: 500ms;
        }
      }

    }
  }

  .crab-rocket {
    position: absolute;
    overflow: visible;
    left: 35px;
    top: 27px;
    z-index: 0;
    transition: transform 1500ms cubic-bezier(0.55, 0.055, 0.675, 0.19);

    rect {
      fill: $clr-red;
      &.step_1 {
        animation-name: opacity-toggle-1;
        animation-duration: 200ms;
        animation-timing-function: step-start;
        animation-iteration-count: infinite;
      }
      &.step_2 {
        animation-name: opacity-toggle-2;
        animation-duration: 200ms;
        animation-timing-function: step-start;
        animation-iteration-count: infinite;
      }
    }
    circle {
      stroke: $clr-red;
      opacity: 1;
      transition: r cubic-bezier(0.215, 0.61, 0.355, 1) 500ms, opacity cubic-bezier(0.215, 0.61, 0.355, 1) 500ms;
    }

    &.bang {
      circle {
        r: 100px;
        opacity: 0;
      }
      rect {
        opacity: 0;
        animation: none;
      }
    }
  }

  .separator {
    .rub {
      width: 5px;
      height: 5px;
      background: $clr-white;
      position: absolute;
      transform: translate3d(0,0,0);
      transition: transform cubic-bezier(0.215, 0.61, 0.355, 1) 600ms;
      @for $i from 1 through 20 {
        &:nth-child(#{$i}) {
          top: random(5)+px;
          left: random(5)+20+px;
          opacity: random();
        }

        &.go:nth-child(#{$i}) {
          transform: translate3d(random(300)+px,- + random(100)+px,0);
        }
      }

    }

  }

}

@keyframes move-2-px-l {
  0%    {transform: translateX(0)}
  50%   {transform: translateX(200%)}
  100%  {transform: translateX(0)}
}
@keyframes move-2-px-r {
  0%    {transform: translateX(0)}
  50%   {transform: translateX(-200%)}
  100%  {transform: translateX(0)}
}
@keyframes move-1-px-l {
  0%    {transform: translateX(0)}
  50%   {transform: translateX(-100%)}
  100%  {transform: translateX(0)}
}
@keyframes move-1-px-r {
  0%    {transform: translateX(0)}
  50%   {transform: translateX(100%)}
  100%  {transform: translateX(0)}
}
@keyframes opacity-toggle-1 {
  0%    {opacity: 1;}
  50%   {opacity: 0;}
  100%  {opacity: 1;}
}
@keyframes opacity-toggle-2 {
  0%    {opacity: 0;}
  50%   {opacity: 1;}
  100%  {opacity: 0;}
}
@keyframes clip {
  0%    {transform: scaleY(1);}
  50%   {transform: scaleY(2);}
  100%  {transform: scaleY(1);}
}


















