.contact-form {
  height: auto;
  overflow: hidden;
  width: 100%;
  height: 0;
  position: relative;
  background: $clr-white;

  .scaler {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: $clr-red;
    z-index: 5;
    transition: transform ease 400ms;
    transform-origin: bottom;

    &.hide {
      transform: scaleY(0);
    }
  }

  form {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;
    width: 100vw;
  }

  h3 {
    max-width: 580px;
    color: $bg-dark;
    font-size: 32px;
    font-weight: bold;
    font-family: $font;
    text-align: center;
    line-height: 1.2;
    margin-bottom: 4vh;
    &:last-child {
      margin-top: 5vh;
    }
    @include breakpoint($mq-mobile) {
      font-size: 18px;
    }
  }

  .form-row {
    width: 485px;
    margin-bottom: 20px;
    height: 68px;
    position: relative;
    text-align: center;
    max-width: 90%;
    @include breakpoint($mq-mobile) {
      height: 40px;
      margin-bottom: 10px;
    }
    input {
      height: 100%;
      width: 100%;
      border: none;
      outline: none;
      background: $clr-lite-grey;
      padding: 0 25px;
      font-family: $font;
      font-size: 14px;
      font-weight: bold;
      color: $bg-dark;
      @include breakpoint($mq-mobile) {
        font-size: 12px;
      }
    }
    button {
      border: none;
      outline: none;
      font-weight: 400;
    }
  }

  &__close {
    margin: 12vh 0 8vh;
    width: 20px;
    height: 20px;
    position: relative;
    cursor: pointer;
    &:before,
    &:after {
      content: '';
      display: block;
      position: absolute;
      width: 100%;
      height: 2px;
      background: $bg-dark;
      left: 0;
      top: 50%;
    }
    &:before {
      transform: rotate(45deg);
    }
    &:after {
      transform: rotate(-45deg);
    }
    &:hover {
      &:before,
      &:after {
        background: $clr-red;
      }
    }
  }
}