.header {
  height: 90px;
  position: fixed;
  //position: absolute;

  opacity: 0;
  transform: translateY(-100%); // !!!
  .logo-animation-over & {
    opacity: 1;
    transform: translateY(0);
  }

  left: 0;
  top: 0;
  z-index: 100;
  transition: height ease 200ms, transform ease 300ms, opacity ease 300ms;
  overflow-y: hidden;
  overflow-x: visible;
  @include breakpoint(1550px) {
    justify-content: flex-start;
  }
  @include breakpoint($mq-desktop-hd) {
    height: 70px;
  }
  @include breakpoint($mq-tablet) {
    height: 50px;
  }
  @include breakpoint($mq-tablet-small) {
    height: 45px;
  }

  & > .site-width {
    padding: 0 115px;
    width: 1400px;
    position: relative;
    background: $clr-white;
    transition: all ease 200ms;

    @include breakpoint(1800px) {
      width: 1300px;
      padding: 0 65px;
    }
    @include breakpoint(1700px) {
      width: 1170px;
      padding: 0 15px;
    }
    @include breakpoint(1550px) {
      width: calc(100% - 200px);
    }
    @include breakpoint($mq-tablet-small) {
      width: 100%;
      &:before {
        content: none;
      }
    }


    &:before {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      right: 100%;
      height: 100%;
      width: calc(50vw - 50%);
      background: $clr-white;
    }
  }

  &__logo {
    float: left;
    display: flex;
    height: 100%;
    align-items: center;

    svg {
      fill: $clr-red;
      transition: fill ease 300ms;
    }

    &-logo {
      padding: 0 20px;
      margin-left: -20px;
      height: 100%;
      display: flex;
      align-items: center;
      z-index: 5;
      svg {
        height: 52px;
        @include breakpoint($mq-desktop-hd) {
          height: 40px;
        }
      }
    }
    &-sweeter {
      margin-right: 40px;
      z-index: 5;
      svg {
        height: 16px;
        @include breakpoint($mq-desktop-hd) {
          height: 14px;
        }
      }
    }
  }

  nav {
    float: right;
    position: relative;
    height: 100%;
    @include breakpoint($mq-tablet-small) {
      display: none;
    }


    a {
      display: flex;
      position: relative;
      align-items: center;
      justify-content: center;
      height: 100%;
      float: left;
      padding: 0 25px;
      //text-shadow: 0 0 0 rgba(0,0,0,.5);
      @include breakpoint($mq-desktop-hd) {
        font-size: 14px;
      }
      @include breakpoint($mq-tablet) {
        font-size: 13px;
        padding: 0 15px;
      }

      //&:last-child {
      //  padding-right: 0;
      //}
      
    }

    .nav-pointer {
      position: absolute;
      display: block;
      width: 10px;
      height: 10px;
      bottom: -1px;
      left: 0;
      margin-left: -5px;
      background: $clr-red;
      transform: translateX(42px);
      transition: transform ease 300ms;
    }

  }

  &__social {
    position: absolute;
    height: 100%;
    top: 0;
    left: 100%;
    margin-left: 20px;
    display: flex;
    justify-content: space-between;
    width: 150px;

    a {
      display: flex;
      height: 100%;
      justify-content: center;
      align-items: center;
      padding: 0 13px;
      transition: transform ease 200ms;
      svg {
        fill: $clr-white;
        transition: fill ease 200ms;
      }
      &:hover {
        svg {
          fill: $clr-black;
        }
      }
      &.hide {
        transform: translateY(-100px);
      }

      @for $i from 1 through 3 {
        &:nth-child(#{$i}) {
          transition-delay: 300/$i+ms;
        }
      }

    }


  }


  &__menu-cover {
    background: $clr-red;
    position: absolute;
    left: 0;
    top: 100%;
    width: calc(700px + 50%);
    height: 100%;
    transition: transform ease 300ms, z-index step-end 400ms;
    z-index: 1;
    overflow: hidden;

    @include breakpoint(1800px) {
      width: calc(650px + 50%);
    }
    @include breakpoint(1700px) {
      width: calc(585px + 50%);
    }
    @include breakpoint(1550px) {
      width: calc(100% - 200px);
    }
    @include breakpoint($mq-tablet-small) {
      width: 100%;
    }


  }

  &__menu-trigger {
    position: absolute;
    display: flex;
    height: 100%;
    width: 60px;
    top: 0;
    left: 100%;
    justify-content: center;
    align-items: center;
    transform: translateX(0);
    transition: transform ease 200ms 1300ms;

    .ico-next {
      &:before,
      &:after,
      &>div:before,
      &>div:after {
        //background: $clr-red;
      }
    }

  }

  &.not-home {
    height: 60px;
    .header__logo {
      svg {
        transition: fill ease 300ms 500ms;
        //fill: $clr-white;
      }
    }
  }
  &.header-hide {
    .header__logo {
      svg {
        transition: fill ease 300ms 500ms;
        fill: $clr-white;
      }
    }
  }

  &-hide {
    & > .site-width {
      background: transparent;
      &:before {
        opacity: 0;
        pointer-events: none;
      }
    }
    .scroll-hide {
      opacity: 0;
      pointer-events: none;
    }
    .header__menu-trigger {
      transform: translateX(-100%);
    }
  }

}